@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

header {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: $dark-color; }

a {
  color: $dark-color;
  transition: all .4s ease;
  &:hover {
    text-decoration: none;
    color: $main-color; } }

.stretching {
  background-color: #2bc2d9;
  height: 30px;
  text-align: center;
  a {
    color: #fff;
    font-size: 16px;
    cursor: default;
    &:hover {
      color: #fff; } } }

.primary {
  background-color: #fff;
  .inner-primary {
    max-width: 1440px;
    width: 100%;
    height: 65px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    position: relative;
    .logo {
      color: #fff;
      background-color: $main-color;
      width: 200px;
      height: 35px;
      margin-left: 28px;
      margin-right: 20px;
      text-align: center;
      border-radius: 8px;
      vertical-align: middle;
      line-height: 2;
      cursor: default; }
    ul {
      margin: 0;
      margin-left: auto;
      margin-right: 60px;
      li {
        display: inline-block;
        margin: 0 20px;
        line-height: 1.8; } }
    .contacts {
      font-size: 19px;
      line-height: 1.8;
      font-weight: 700;
      margin-right: 28px;
      a:hover {
        color: $dark-color;
        cursor: default; } } } }

.secondary {
  background-color: #777;
  .secondary-inner {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    height: 55px;
    display: flex;
    color: #fff;
    align-items: center; } }

.categories-header {
  background-color: #666;
  height: 55px;
  display: flex;
  align-items: center;
  padding: 0 28px;
  font-size: 19px;
  cursor: default;
  transition: all .5s ease;
  &:hover {
    background-color: #555; }
  span {
    width: 24px;
    height: 1px;
    background-color: #fff;
    position: relative;
    margin-right: 10px;
    margin-bottom: 1px;
    &:before {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      top: 6px; }
    &:after {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      bottom: 6px; } } }

.favs-and-cart {
  justify-content: center;
  display: flex;
  .favs {
    background: url('../../assets/heart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    margin-left: 35px;
    margin-right: 20px;
    cursor: pointer; }
  .cart {
    background: url('../../assets/shopping-cart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    cursor: pointer; } }

.btn-clear {
  border: none;
  &:hover {
    border: none; } }

.modal-close {
  background: url('../../assets/cancel.svg') no-repeat;
  background-position: center;
  width: 32px;
  height: 32px;
  display: block;
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer; }

.search-block {
  z-index: 10;
  position: absolute;
  margin-left: 27.5%;
  width: 45%;
  font-family: 'Roboto', sans-serif;
  color: $dark-color; }


.search-ul {
  padding: 0;
  max-height: 440px;
  overflow: hidden;
  background-color: #fefefe;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12); }

.search-li {
  list-style-type: none;
  display: flex;
  padding: 5px 15px 10px 25px;
  background-color: #fefefe;
  align-items: center;
  position: relative;
  border: solid 1px #fff;
  transition: all .3s ease;
  border-radius: 5px;
  &:hover {
    border: solid 1px $main-color;
    .preview-link {
      display: block; } }
  &:first-child {
    padding-top: 15px;
    .preview-link {
      top: 40px; } }
  .preview-body {
    padding-left: 30px;
    .preview-text {
      margin-right: 25px; }
    .preview-price {
      margin-right: 0; } } }

.preview-link {
  display: none;
  transition: all .3s ease;
  background: url('../../assets/right-arrow-long.svg') no-repeat;
  background-position: center;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 20px;
  top: 30px;
  cursor: pointer; }
